import React, {Fragment, useEffect, useState} from "react";
import "@pages/popup/Popup.css";
import {TopPanel} from "../../components/TopPanel";
import {Account} from "@front-lib";

const Popup = () => {
  const [accounts,setAccounts]= useState<any[]>([])
  const [isHunter,setIsHunter]= useState(false)
  useEffect(() => {

    async function load() {
      const value = await Account.getAccountsData()
      setAccounts(value)
      chrome.storage.sync.get({"is_hunter_user": false}, function (value) {
        if (value.is_hunter_user) {
          setIsHunter(true)
        }
      })
    }
    load().then()
  }, []);

  return <Fragment>
    <TopPanel/>
  </Fragment>;
};

export default Popup;
